<template>

    <div class="user-profile">
        <div class="container no-padding">
            <div class="top-buttons" v-if="links.length>0">
                <button  v-for="link in links" :class="link.class">
                    <router-link :to="link.link_url">{{link.link_title}}</router-link>
                </button>

            </div>

            <div class="tab" v-for="tab in tabs">
                <div class="block" v-for="(block,index) in tab.blocks">
                    <div v-if="photo_block" class="col-md-4">

                        <div class="user-photo">

                            <div class="photo-block">
                                <!--<img id="profile-photo" style="width: 100%; height: 100%;" :src="photo_path" alt="">-->
                                <div id="profile-photo">

                                </div>
                            </div>

                            <div class="photo-wrapper">
                                <p class="user-name">
                                    <span v-if="models.Consumers[0].first_name">{{models.Consumers[0].first_name}} </span>
                                    <span v-if="models.Consumers[0].last_name"> {{models.Consumers[0].last_name}}</span>
                                </p>

                                <div class="photo-buttons">
                                    <label for="photo-load" class="btn btn-green">
                                        Загрузить
                                        <input id="photo-load" style="display: none;" type="file" @change="photoLoad">
                                    </label>
                                    <button class="btn btn-dark " @click="deletePhoto">Удалить</button>
                                </div>
                            </div>

                        </div>
                    </div>
                    <div :class="{'col-md-12':photo_block, 'col-md-16':!photo_block, 'no-padding':true, }">
                        <div class="profile-card">
                            <div class="fields">
                                <Zone
                                        :models="models"
                                        :fields="fields.filter(field=> field.zone==='1')"
                                        :add_models="add_data_models"
                                        :class="{'full-width':fields.filter(field=> field.zone==='2').length===0, 'grid': form_parameters.use_grid_layout}"
                                >

                                </Zone>

                                <Zone v-if="fields.filter(field=> field.zone==='2').length>0"
                                      :models="models"
                                      :fields="fields.filter(field=> field.zone==='2')"
                                      :add_models="add_data_models"
                                      :class="{'grid': form_parameters.use_grid_layout}"
                                >
                                </Zone>


                            </div>
                            <div class="card-buttons">
                                <!--<div class="contractor_checkbox checkbox">-->
                                <!--<label>-->
                                <!--<input type="checkbox">-->
                                <!--<span class="checkbox-custom"><i class="fa fa-check"></i></span>-->
                                <!--<span> {{actual}}</span>-->
                                <!--</label>-->
                                <!--</div>-->

                                <div class="card-buttons-list">
                                    <a v-for="action in sets.profile_card_actions" :name="action.code" :class="action.class"
                                       @click="sendProfile(action)">{{action.title}}</a>
                                </div>

