<script>
    import {en, ru} from 'vuejs-datepicker/dist/locale'
    import axios from 'axios';
    import Datepicker from 'vuejs-datepicker';
    import {EventBus} from '../main';
    import Zone from './Zone';
    export default {
        data: function () {
            return {
                modelObj: {},
                tabs: [],
                models: {},
                add_data_models:{},
                actions: [],
                form_parameters: {},
                sets: {},
                links: [],
                actual: '',
                datapicker_translations: {
                    en: en,
                    ru: ru,
                },
                photo_block: null,
                photo_path: '',
                fields:[]
            }
        },
        methods: {

            fillComponent() {
                this.$http.post(this.$bRoute.meta.route)
                    .then(res => {
                        this.tabs = res.data.tabs;
                        this.models = res.data.main_data_models;
                        this.add_data_models = res.data.add_data_models;
                        this.actions = res.data.actions.actions_card;
                        this.links = res.data.links;
                        this.form_parameters = res.data.form_parameters;
                        this.sets = res.data.sets;
                        this.photo_block = res.data.form_parameters.photo_block;
                        this.photo = res.data.main_data_models.Consumers[0].photo;
                        this.fields = [];
                        this.tabs[0].blocks.forEach(block => {

                                this.fields = [...this.fields, ...block.block_fields];
                        });

                        this.$nextTick(() => {

                    let elem = document.querySelector('#profile-photo');
                    if(elem)elem.style.backgroundImage = this.photo?'url(' + this.photo + ')':'url(/img/user_default.png)';

                            // if (this.photo) document.querySelector('#profile-photo').style.backgroundImage = 'url(' + this.photo + ')'; //устанавливаем фотографию при загрузке
                            // else{
                            //     document.querySelector('#profile-photo').style.backgroundImage = 'url(/img/user_default.png)'; //устанавливаем фотографию при загрузке
                            // }
                        });
                    })
                    .catch(error=>{
                        this.$store.dispatch('showToast',{
                            template:  'error',
                            title:     error.response.status,
                            message:   error.response.data.message,
                            position:  error.response.data.position,
                        })
                    })
            },

            async test(item) {
                var r = await this.$parent.$refs.dmodal.showmodal(item);
            },
            async confirm(modal) {
                var item = {
                    modal: {
                        confirm: true,
                        html: '<h1> Внимание </h1> <p>Спасибо за внимание</p>',
                        actionOk: {
                            title: 'агась'
                        }
                    }
                };

                var r = await this.$parent.$refs.dmodal.confirmmodal(modal);

            },
            async sendProfile({code, link}) {

                if(code==='back'){
                    this.$bRouter.go(-1);
                    return;
                }

                const {form_base_data_model} = this.form_parameters;

                var data = {
                    main_data_models: this.models,
                    form_parameters: {form_base_data_model}
                };
                try {
                    var validation = await this.$validator.validateAll();
                    if(!validation){
                        for (var item in this.fieldsVV) {
                            this.fieldsVV[item].touched = true;
                        }
                        this.$toast.error("Некорректные данные", '', {position: 'topRight'})
                        return;
                    }
                    var res = await this.$http.post(link, data);
                    await this.$store.dispatch('showToast', {
                        template: res.data.template,
                        title: res.data.title,
                        message: res.data.message,
                        position: res.data.position,
                    })

                    if (res.data.requery && code !== 'ok') {

                        await this.fillComponent();

                    }

                    if(code==='ok')this.$bRouter.go(-1);

                } catch (error) {
                    this.$store.dispatch('showToast',{
                        template:  'error',
                        title:     error.response.status,
                        message:   error.response.data.message,
                        position:  error.response.data.position,
                    })
                }


            },
            photoLoad: function (e) {
                var img = document.querySelector('#profile-photo');

                var reader = new FileReader();

                reader.readAsDataURL(e.target.files[0]);

                var name = e.target.files[0].name;
                name = name.slice(0, name.lastIndexOf('.'));

                var stored_file = {
                    id: null,
                    storedFile: {
                        size: e.target.files[0].size,
                        name: name,
                        extension: e.target.files[0].name.slice(e.target.files[0].name.lastIndexOf(".") + 1),
                    },
                    stored_file_id: null
                };

                reader.onload = (ev) => {
                    img.style.backgroundImage = 'url(' + ev.target.result + ')';
                    EventBus.$emit('userPhotoChanged', {src:'url(' + ev.target.result + ')'});

                    var mime = ev.target.result.split(',');
                    stored_file.storedFile['mime'] = mime[1];
                    stored_file.storedFile['mime_type'] = mime[0];
                    this.models[this.form_parameters.form_base_data_model][0].stored_file = stored_file;
                };


            },
            deletePhoto() {
                var img = document.querySelector('#profile-photo');
                var img_input = document.querySelector('#photo-load');
                img.style.backgroundImage = 'url(/img/user_default.png)';
                this.models[this.form_parameters.form_base_data_model][0].photo = "";
                img_input.value = '';
                this.models[this.form_parameters.form_base_data_model][0].stored_file_id= null;
                EventBus.$emit('userPhotoChanged', {src:'url(/img/user_default.png)'})
            }
        },

        mounted() {
            EventBus.$on('triggerModelUpdate', (data)=>{
               this.fillComponent();
            });
            /*this.$parent.$refs.dmodal*/

            var photo = false;

            if (this.$bRouter.path == '/profileTest') photo = false;

            if (this.$store.getters.getLang == 'en') this.actual = 'Actual';
            if (this.$store.getters.getLang == 'ru') this.actual = 'Актуальность';

            /*axios.post('/admin/user/card', {photo:photo})*/
            this.fillComponent();
        },
        components: {
            Datepicker,
            Zone
        }
    }

</script>

